import $ from 'dom7';
import Framework7 from 'framework7/bundle';

// Import F7 Styles
import 'framework7/framework7-bundle.css';

// Import Icons and App Custom Styles

import '../css/app.css';


// Import Routes
import routes from './routes.js';
// Import Store
import store from './store.js';

// Import main app component
import App from '../app.f7.html';


const app = new Framework7({
  name: 'Robinson Homebrew', // App name
  theme: 'auto', // Automatic theme detection
  el: '#app', // App root element
  component: App, // App main component
  popup: {
    closeByBackdropClick: false,
  },
  // App store
  store: store,
  // App routes
  routes: routes,
  on: {
    pageInit: function (page) {
      /*app.request.json('static/json/recipes.json')
      .then(function (res) {
        // load recipes in store
        store.dispatch('openRecipeBook', res.data.list);
        // load recipes on page
        store.dispatch('getRecipe', $('.tab-active')[0]);
      });*/
      store.dispatch('getCookieList');
      let cL= { "list": [] };
      store.state.cookieList.forEach(function(item, ind) {
        store.dispatch('b64_to_utf8', item);
        cL.list.unshift(JSON.parse(store.getters.cookieData.value));
      });
      cL.list.sort( (p1, p2) =>  (p1.id > p2.id) ? 1 : (p1.id < p2.id) ? -1 : 0);
      store.dispatch('openRecipeBook', cL.list);
      store.dispatch('getRecipe', $('.tab-active')[0]);
    },
  },
});

// Load recipe details to card before displaying the card
app.on('popupOpen', function (popup) {
  const popUp= popup.el;
  store.dispatch('populatePopup', {popUp});
});

// Clear recipe details from card
app.on('popupClosed', function (popup) {
  $(popup.el).find('.popup-padding:not(.edit)').show();
  $(popup.el).find('.link.color-gray').show();
  $(popup.el).find('.recipePopup .popup-padding').html('');
  store.dispatch('getRecipe', $('.tab-active')[0]);
});
