import { createStore } from 'framework7';

const store = createStore({
  state: {
    recipeList: [],
    beerRecipes: [],
    recipes: [],
    cookieList: [],
    cookieData: '',
  },
  getters: {
    beerRecipes({ state }) {
      return state.beerRecipes;
    },
    recipes({ state }) {
      return state.recipes;
    },
    recipeList({ state }) {
      return state.recipeList;
    },
    cookieData({ state }) {
      return state.cookieData;
    },
  },
  actions: {
    openRecipeBook({ state }, data) {
      state.recipeList= data;
    },
    getCookieList({ state }){
      let ca= document.cookie.split(';');
      let cd= '';
      let cookies= [];
      for(let i= 0; i<ca.length; i++){
        let c= ca[i];
        let n= "b"+i+"=";
        while(c.charAt(0) == ' '){
          c= c.substring(1);
        }
        if(/(b)\d=/.test(c)){
          cd= c.substring(n.length, c.length);
          cookies[i]= (cd);
        }
      }
      state.cookieList= cookies;
    },
    addRecipe({ state }, {$, $f7}) {
      let recipes= store.getters.beerRecipes;
      const newId= (recipes.value.length+1).toString();;
      const newRecipe= {
        "id": newId,
        "title": "New Recipe",
        "ingredients": [],
        "instructions": [],
        "notes": "",
        "source": ""
      };
      state.recipeList.push(newRecipe);
      store.dispatch('getRecipe', '');
    },
    editRecipe({ state, dispatch }, {el, id, $, $f7}) {
      let recipes= store.getters.recipeList;
      let recipeIndex= recipes.value.map(function(e){ return e.id; }).indexOf(id);
      let ingredientsLength= recipes.value[recipeIndex].ingredients.length;
      let directionsLength= recipes.value[recipeIndex].instructions.length;
      let htmlText= '<form class="list no-hairlines-md" id="popup-form"><input type="hidden" class="hidden" name="id" value="'+recipes.value[recipeIndex].id+'" /><ul><li class="item-content item-input item-input-outline item-input-with-value"><div class="item-inner"><div class="item-title item-floating-label">Name</div><div class="item-input-wrap"><input type="text" placeholder="Recipe Name" name="title" value="'+recipes.value[recipeIndex].title+'" class="input-with-value" /><span class="input-clear-button"></span></div></div></li></ul>';
      htmlText+= '<h2 class="block-title">Ingredients:</h2><ul class="ingredientsSection">';
      recipes.value[recipeIndex].ingredients.forEach(function(item, ind) {
        if((typeof(item) !== "undefined") && (item)){
          htmlText+= '<li class="item-content item-input item-input-outline item-input-with-value"><div class="item-inner"><div class="item-title item-floating-label">Ingredient '+(ind+1)+'</div><div class="item-input-wrap"><input type="text" placeholder="Ingredient" name="ingredients'+(ind+1)+'" value="'+ item +'" class="input-with-value" /><span class="input-clear-button"></span></div></div></li>';
        }
      });
      htmlText+= '</ul><a class="button add button-round button-fill color-green" id="ingredientsAdd"><i class="icon f7-icons">plusmark</i></a><hr />';
      htmlText+= '<h2 class="block-title">Directions:</h2><ul class="directionsSection">';
      recipes.value[recipeIndex].instructions.forEach(function(item, ind) {
        if((typeof(item) !== "undefined") && (item)){
          htmlText+= '<li class="item-content item-input item-input-outline item-input-with-value"><div class="item-inner"><div class="item-title item-floating-label">Direction '+(ind+1)+'</div><div class="item-input-wrap"><input type="text" placeholder="Direction" name="instructions'+(ind+1)+'" value="'+ item +'" class="input-with-value" /><span class="input-clear-button"></span></div></div></li>';
        }
      });
      htmlText+= '</ul><a class="button add button-round button-fill color-green" id="directionAdd"><i class="icon f7-icons">plusmark</i></a><hr />';
      if((typeof(recipes.value[recipeIndex].notes) !== "undefined") && (recipes.value[recipeIndex].notes !== '')){
        htmlText+= '<h2 class="block-title">Notes:</h2><ul><li class="item-content item-input item-input-with-value"><div class="item-inner"><div class="item-title item-floating-label">Notes</div><div class="item-input-wrap"><textarea class="resizable" placeholder="Notes" name="notes" class="item-input-with-value">'+recipes.value[recipeIndex].notes+'</textarea><span class="input-clear-button"></span></div></div></li></ul><hr />';
      } else {
        htmlText+= '<h2 class="block-title">Notes:</h2><ul><li class="item-content item-input"><div class="item-inner"><div class="item-title item-floating-label">Notes</div><div class="item-input-wrap"><textarea class="resizable" placeholder="Notes" name="notes"></textarea><span class="input-clear-button"></span></div></div></li></ul><hr />';
      }
      if((typeof(recipes.value[recipeIndex].source) !== "undefined") && (recipes.value[recipeIndex].source !== '')){
        htmlText+= '<h2 class="block-title">Recipe Website:</h2><ul><li class="item-content item-input item-input-outline item-input-with-value"><div class="item-inner"><div class="item-title item-floating-label">Recipe Source</div><div class="item-input-wrap"><input type="text" name="source" placeholder="Recipe Source" value="'+ recipes.value[recipeIndex].source +'" class="input-with-value" /><span class="input-clear-button"></span></div></div></li></ul><hr />';
      } else {
        htmlText+= '<h2 class="block-title">Recipe Website:</h2><ul><li class="item-content item-input item-input-outline"><div class="item-inner"><div class="item-title item-floating-label">Recipe Source</div><div class="item-input-wrap"><input type="text" name="source" placeholder="Recipe Source" /><span class="input-clear-button"></span></div></div></li></ul><hr />';
      }
      htmlText+= '</form><p class="row"><a href="#" class="col button cancel-confirm color-gray">Cancel</a><a href="#" class="col button edit-save button-fill color-blue">Save</a></p><br><p class="row"><a href="#" class="col button edit-delete button-outline color-red">Delete</a></p>';
      $(el).find('.link.color-gray').hide();
      $(el).find('.popup-padding:not(.edit)').hide();
      $(el).find('.recipePopup .popup-padding.edit').html(htmlText);
      $('.cancel-confirm').on('click', function () {
        $f7.dialog.confirm('Cancel changes?', '', function () {
          $(el).find('.popup-padding.edit').html('');
          $(el).find('.link.color-gray').show();
          $(el).find('.popup-padding:not(.edit)').show();      
        });
      });
      $('.edit-save').on('click', function () {
        let formData = $f7.form.convertToData('#popup-form');
        let ingredientsArr= [];
        let instructionsArr= [];
        Object.keys(formData).forEach(function(item, ind) {if(/(ingredients)\d/.test(item)){ingredientsArr.push(item);}});
        Object.keys(formData).forEach(function(item, ind) {if(/(instructions)\d/.test(item)){instructionsArr.push(item);}});
        formData.ingredients= [];
        formData.instructions= [];
        for(let i= 0; i<ingredientsArr.length; i++){
          let ingItem= ingredientsArr[i];
          if(formData[ingItem]){
            formData.ingredients.push(formData[ingItem]);
          }
          delete formData[ingItem];
        }
        for(let i= 0; i<instructionsArr.length; i++){
          let insItem= instructionsArr[i];
          formData.instructions.push(formData[insItem]);
          delete formData[insItem];
        }
        dispatch('updateRecipe', {id, formData, recipeIndex});
        const popUp= document.querySelector('.recipePopup[data-id="'+id+'"]');
        dispatch('populatePopup', {popUp});
        
        $(el).find('.popup-padding.edit').html('');
        $(el).find('.link.color-gray').show();
        $(el).find('.popup-padding:not(.edit)').show();
      });
      $('#ingredientsAdd').on('click', function () {
        ingredientsLength++;
        $('.ingredientsSection').append('<li class="item-content item-input item-input-outline"><div class="item-inner"><div class="item-title item-floating-label">Ingredient '+ingredientsLength+'</div><div class="item-input-wrap"><input type="text" placeholder="Ingredient" name="ingredients'+ingredientsLength+'" /><span class="input-clear-button"></span></div></div></li>');
      });
      $('#directionAdd').on('click', function () {
        directionsLength++;
        $('.directionsSection').append('<li class="item-content item-input item-input-outline"><div class="item-inner"><div class="item-title item-floating-label">Direction '+directionsLength+'</div><div class="item-input-wrap"><input type="text" placeholder="Direction" name="instructions'+directionsLength+'" /><span class="input-clear-button"></span></div></div></li>');
      });
      $('.edit-delete').on('click', function () {
        state.recipeList.splice(recipeIndex, 1);
        document.cookie= "b"+id+"=; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/";
        $f7.popup.close('.recipePopup');
      });
    },
    getRecipe({ state }, tabEl) {
      state.beerRecipes= [];
      state.recipeList.forEach(function(item, index){
        state.beerRecipes.push(item);
      })
    },
    searchRecipe({ state }, query) {
      state.recipes= [];
      const queryItem= query.toLowerCase();
      state.recipeList.forEach(function(item, index){
        const title= item.title.toLowerCase();
        if(title.indexOf(queryItem) !== -1){
          state.recipes.push(item);
        }
      })
    },
    updateRecipe({ state, dispatch }, data){
      state.recipeList[data.recipeIndex]= data.formData;
      store.dispatch('getRecipe', '');
      const text= JSON.stringify(data.formData);
      dispatch('utf8_to_b64', text);
      const d= new Date();d.setTime(d.getTime()+(365*864e5));
      const exp= "expires="+d.toUTCString();
      document.cookie= "b"+data.id+"="+state.cookieData+";"+exp+";path=/";
    },
    populatePopup({ state, dispatch }, param){
      let popUp= param.popUp;
      let thisId= popUp.dataset.id;
      let thisType= popUp.dataset.recipe;
      let recipes = [];
      (typeof(thisType) !== "undefined") ? recipes = store.getters[thisType+'Recipes'] : recipes = store.getters.recipes;
      let index= recipes.value.map(function(e){ return e.id; }).indexOf(thisId);
      let htmlText= '<h1 class="popup-header">'+ recipes.value[index].title +'</h1>'
      if((typeof(recipes.value[index].ingredients) !== "undefined") && (recipes.value[index].ingredients[0])){
        htmlText+= '<h2>Ingredients:</h2><ul>';
        recipes.value[index].ingredients.forEach(function(item, ind) {
          htmlText+= '<li>'+item+'</li>';
        });
        htmlText+= '</ul><hr />';
      }
      if((typeof(recipes.value[index].instructions) !== "undefined") && (recipes.value[index].instructions[0])){
        htmlText+= '<h2>Directions:</h2><ol>';
        recipes.value[index].instructions.forEach(function(item, ind) {
          htmlText+= '<li>'+item+'</li>';
        });
        htmlText+= '</ol><hr />';
      }
      if((typeof(recipes.value[index].notes) !== "undefined") && (recipes.value[index].notes !== '')){
        htmlText+= '<h2>Notes:</h2><p class="notes">'+recipes.value[index].notes+'</p><hr />';
      }
      if((typeof(recipes.value[index].source) !== "undefined") && (recipes.value[index].source !== '')){
        htmlText+= '<div class="list"><ul><li><a class="item-link external text-color-blue" rel="noreferrer noopener" target="_blank" title="Link opens in a new tab" href='+recipes.value[index].source+'><div class="item-inner"><div class="item-title">Recipe Website</div></div></a></li></ul></div>';
      }
      popUp.querySelector('.popup-padding:not(.edit)').innerHTML= htmlText;
    },
    check(){
      if(localStorage.getItem('tacos')){
        var myObject = JSON.parse(localStorage.getItem('tacos'));
        mainList = myObject;
        restarted = true;
        showData();
        localStorage.removeItem('tacos');
      } else if(localStorage.getItem('custom')){
        document.getElementById("txtOption").focus();
      }
      if(paramURL.has('l') && paramURL.has('r')){
        mainList= JSON.parse(b64_to_utf8(paramURL.get('l')));
        finalResults= JSON.parse(b64_to_utf8(paramURL.get('r')));
        initList();
      }
    },
    utf8_to_b64({ state }, str) {
      state.cookieData= window.btoa(unescape(encodeURIComponent(str)));
    },
    b64_to_utf8({ state }, str) {
      state.cookieData= decodeURIComponent(escape(window.atob(str)));
    },
    makeUrlParams(){
      var currentUrl= window.location.origin + window.location.pathname;
      var mL= utf8_to_b64(JSON.stringify(mainList));
      var fR= utf8_to_b64(JSON.stringify(finalResults));
      return currentUrl+'?l='+mL+'&r='+fR;
    },
    importRecipes({ state }, {$, $f7}){
      $f7.request.json('static/json/recipes.json')
      .then(function (res) {
        // load recipes in store
        store.dispatch('openRecipeBook', res.data.list);
        // load recipes on page
        store.dispatch('getRecipe', $('.tab-active')[0]);
      });
    },
  },
})
export default store;



